import React, {Component, Fragment } from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import Form from 'react-bootstrap/Form';
import lock from '../../../images/padlock.png';
import { Link } from 'react-router-dom';
import './Login.css';
import '../../../assets/Common.css'

import {
    ToastContainer,
    toast,
    Bounce,
    Slide,
    Flip,
    Zoom
} from 'react-toastify';

import 'react-toastify/dist/ReactToastify.css';

import withRouter from '../../../config/withRouter';

import {
    Row, Col,
    Button,
} from 'reactstrap';

class BasicLogin extends Component {
    constructor() {
        super();
        this.state = {
            id: "",
            pw: "",
        }; 
    }

    onChange = (e) => {
        this.setState({
            id: e.target.value
        });
        console.log(e);
    };

    onClick = (e) => {
        const { history } = this.props;
        const { id, pw } = this.state;
        if(id == "admin" && pw == "1234") {
            history.push('../Members/basic');
        }
        else {
            toast.warn('아이디 또는 패스워드가 올바르지 않습니다.');
        }
    };

    render() {
        return (
            <Fragment>
                <TransitionGroup>
                    <CSSTransition
                        component="div"
                        className="TabsAnimation"
                        appear={true}
                        timeout={0}
                        enter={false}
                        exit={false}>
                        <div>
                        <Row className='header font-size-large'>
                            <Col md={10}>
                                <div>관리자페이지</div>
                            </Col>
                        </Row>
                        <div className='body'>
                            <div className='space-10px'></div>
                            <Row className='login'>
                                <Col>
                                    <div className='login1'>로그인</div>
                                </Col>
                            </Row>
                            <div className='boader-gray'></div>
                            <div className='space-50px'></div>
                            <div className='horizontal-center'>
                                <img src={lock} className="lock-image" alt='lock'/>
                            </div>
                            <div className='space-30px'></div>
                            <div className='horizontal-center'>Member Login</div>
                            <div className='space-20px'></div>
                          <Form className='gap'>
                              <Form.Group>
                                   <Row>
                                       <Col md={4}></Col>
                                      <Col md={1}>
                                          <Form.Label className='text-left'>아이디</Form.Label>
                                     </Col>
                                     <Col md={3}>
                                            <Form.Control placeholder="아이디를 입력하세요." onChange={(e) => this.setState({id: e.target.value})} />
                                       </Col>
                                       <Col md={4}></Col>
                                   </Row>
                               </Form.Group>

                                <Form.Group>
                                  <Row>
                                     <Col md={4}></Col>
                                      <Col md={1}>
                                          <Form.Label className='text-left'>비밀번호</Form.Label>
                                     </Col>
                                     <Col md={3}>
                                           <Form.Control type="password" placeholder="비밀번호를 입력하세요." onChange={(e) => this.setState({pw: e.target.value})}/>
                                      </Col>
                                      <Col md={4}></Col>
                                   </Row>
                              </Form.Group>
                               <Row>
                                   <Col md={4}></Col>
                                   <Col md={4}>
                                       <Button className='full-column' variant="primary" type="button" onClick={this.onClick}>로그인</Button>
                                   </Col>
                                  <Col md={4}></Col>
                                </Row>
                            </Form>
                            <div className='space-20px'></div>
                            <div className='boader-gray'></div>
                            <div className='footer'>Copyright 2023 SLTWELLNESS All Right Reserved.</div>
                            </div>
                        </div>
                       
                    </CSSTransition>
                </TransitionGroup>
            </Fragment>
        )
    }
}

export default withRouter(BasicLogin);
